.show-now{
    .container{
        max-width: 100vw;
        max-height: 984px;
        .show-now-word{
            max-width: 71%;
        }
        .collection{
            height: 120px;
            span{
                // width: 100px;
                height: 100%;
                border: 1px solid #000;
                line-height: 2.4;
            }
            p{
                border: 1px solid #000;
                margin: 0;
                width: 100%;
            }
        }
    }
}

